import React, { useState, useEffect } from 'react';
import bsCustomFileInput from 'bs-custom-file-input';
import axios from 'axios';
import { baseUrl } from '../config/config'
import DataTable from 'react-data-table-component';

export default function LsUsers() {
  const [listorg, setListorg] = useState([])
  const getData = () => {
    axios.get(baseUrl + 'users').then((res => {
      setListorg(res.data)
    }))
  }
  useEffect(() => {
    getData()
  }, [listorg])
  const SubmitDelete = (token) => {
    axios.delete(baseUrl + `users/${token}`)
      .then((res => {
        if (res.data.reussie) alert(res.data.reussie)
        else (alert(res.data.erreur))
        getData()
      })
      ).catch((err) => {
        alert(err)
      })
  }
  const accept = (id, nom) => {
    if (window.confirm("Voulez vous supprimer l'utilisateur " + nom)) {
      SubmitDelete(id)
    }
  }
  const data = listorg;
  const columns = [
    {
      name: 'N°',
      selector: (row, index) => index + 1,
      width: '50px'
    },
    {
      name: 'Nom',
      selector: row => row.nom_complet,
      sortable: true,
      width: '150px'
    },
    {
      name: 'Login',
      selector: row => row.login,
      width: '150px'
    },
    {
      name: 'Role',
      selector: row => row.role,
      width: '150px'
    },
    {
      name: 'Grade',
      selector: row => row.grade,
      width: '180px'
    },
    /* {
      name: 'Date enregistrement',
      selector: row => row.date_creation,
      width: '150px'
    }, */
    {
      name: 'Action',
      cell: row => <span><a className="badge badge-success" href={"/users/MdUsers/" + `${row.id_users}`} title="Modifier les infos de l'utilisateur"><i className="mdi mdi-lead-pencil mdi-icon"></i></a> &nbsp;
        <a className="badge badge-success" href={"/users/Mduserspass/" + `${row.id_users}`} title="Modifier le Mot de passe de l'utilisateur"><i className="mdi mdi-table-edit mdi-icon"></i></a> &nbsp;
        <button type="button" className="badge badge-danger" onClick={() => accept(row.id_users, row.nom_complet)} title="Supprimer l'utilisateur"><i className="mdi mdi-delete-forever mdi-icon"></i></button></span>,
      width: '150px'
    },
  ]

  return (
    <div>
      <div className="page-header">
        <h3 className="page-title">Organisateur </h3>
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><a href="#">Organisateur</a></li>
            <li className="breadcrumb-item active" aria-current="page">Liste</li>
          </ol>
        </nav>
      </div>
      <div className="row">
        <div className="col-12 grid-margin">
          <div className="card">
            <div className="card-body">
              <h4 className="card-title">Liste des organisateurs</h4>
              <div className="table-responsive">
                {/* <table className="table">
                  <thead>
                    <tr>
                      <th>N°</th>
                      <th>nom</th>
                      <th>tel</th>
                      <th>email</th>
                      <th>adresse</th>
                      <th>Date enregistrement</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    {listorg.map((val, index) => {
                      return (
                        <tr>
                          <td>{index + 1}</td>
                          <td>{val.nom}</td>
                          <td>{val.tel}</td>
                          <td>{val.email}</td>
                          <td>{val.adresse}</td>
                          <td>{val.date}</td>
                          <td>
                            <a className="badge badge-success" href={"/organisateur/MdOrganisateur/" + `${val.token}`} title="Modifier les infos de l'organisateur"><i className="mdi mdi-lead-pencil mdi-icon"></i></a> &nbsp;
                            <a className="badge badge-info" href={"/organisateur/MdOrgpass/" + `${val.token}`} title="Modifier les mot de passe de l'organisateur"><i className="mdi mdi-table-edit mdi-icon"></i></a> &nbsp;
                            <button type="button" className="badge badge-danger" onClick={() => accept(val.token, val.nom)} title="Supprimer l'organisateur"><i className="mdi mdi-delete-forever mdi-icon"></i></button>
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                  </table> */}
                <DataTable
                  keyField
                  columns={columns}
                  data={data}
                  pagination
                  fixedHeader
                  highlightOnHover
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
